import { Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { MAT_SNACK_BAR_DATA, MatSnackBarRef } from '@angular/material/snack-bar';

import { DscSnackbarConfig } from './shared/dsc-snackbar-config';

@Component({
  selector: 'dsc-snackbar',
  templateUrl: './dsc-snackbar.component.html',
  styleUrls: ['./dsc-snackbar.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DscSnackbarComponent implements OnInit {

  matIconName!: string;

  constructor(
    @Inject(MAT_SNACK_BAR_DATA) public snackbarConfig: DscSnackbarConfig,
    private _snackbarRef: MatSnackBarRef<DscSnackbarComponent>
  ) {}

  ngOnInit(): void {
    switch (this.snackbarConfig.data.variant) {
      case 'info':
        this.matIconName = 'info';
        break;
      case 'danger':
        this.matIconName = 'error';
        break;
      case 'success':
        this.matIconName = 'check_circle';
        break;
      case 'warning':
        this.matIconName = 'warning';
        break;
    }
  }

  close(): void {
    this._snackbarRef.dismiss();
  }
}
